import {playMode} from './config'
import {getSearchHistory} from '../common/js/cache'

const state = {
  singer: {},
  playingState: false,
  fullScreen: false,
  playlist: [],
  sequenceList: [],
  mode: playMode.sequence,
  currentIndex: -1,
  topList: [],
  searchHistory: getSearchHistory()
}
export default state



